@charset "utf-8";

#container{
  margin-top: 100px;
}

/* project tab서식 */
.is-active{
  font-weight: bold;
  border-bottom: solid 3px #757464;
  color:#F2E5BD;
  background-color: #F2E5BD;
}



.tab_cont_wrap, .tab_cont_wrap4{
  display: flex;
  margin: 0 auto;
  width: 1000px;
  height: 600px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.con_wrap{
  display: flex;
  justify-content: center;
}
.con_wrap li{
  cursor: pointer;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  font-size: 18px;  
  color: #736B66;
  padding: 10px 59px;
}

.con_wrap li:hover{
  transition: .3s;
  background-color: #736b66;
  color:#F2E5BD;
}
.link_wrap{
  border-radius: 5px;
  background-color: #F2E5BD;
  border: solid 1px #736B66;
  display: block;
  width:130px; height: 40px;
  line-height: 40px;
  text-align: center;
}

.link_wrap:hover{
  background-color: rgb(255, 242, 179);
}

.btn_wrap{
  display: flex;
  margin-top: 50px;
}
.website_btn{
  color: #736B66;
}

.con_txt{
  width: 600px;
  margin-top: 60px;
  padding: 10px;
  box-sizing: border-box;
  line-height: 35px;
}


.mobile_ofd{
  margin-left: 127px;
}

/* 개인프젝 */
.con_wrap2{
  display: grid;
  width: 1000px;
  padding: 10px;
  grid-template-columns: repeat(2,auto);
  grid-template-rows: repeat(2,300px);
  margin: 0 auto;
}
.con_wrap2 li{
  width: 495px;
  height: 290px;
  overflow: hidden;
}
figure{
  position: relative;
}
.detail_btn{
  font-size: 18px;
  display:block;
  width: 150px;
  height: 50px;
  color: #fff;
  border: solid 1px #fff;
  margin: 20px auto;
  text-align: center;
  line-height: 50px;
  transition: 1s;
}
.detail_btn:hover{
  background-color: #fff;
  color: #333;
  border-color: #333;
  font-weight: 600;
}

.project_title{
  font-size: 30px;
  transform: translate(-100px,-10px);
}

.project_desc{
  display: inline-block;
  text-justify: auto;
  font-weight: normal;
  text-align: left;  

}
.con_wrap2 img{
  width: 100%;
}
.con_wrap2 img:hover{
  scale: 1.1;
  transition: .3s;

}


.item{
  display: flex;
  width: 1000px;
  justify-content: space-between;
  margin: 60px auto;
  padding-left: 50px;
  padding-bottom: 50px;

}
.cont_img{
  width: 550px;
  overflow: hidden;
}
.img1 img{
  width: 180%;
  height: 180%;
  transform: translate(-220px,-120px);
}
.img2 img{
  width: 160%;
  height: 160%;
  transform: translate(-210px,-120px);
}
.img3 img{
  height: 130%;
  transform: translate(-100px,-80px);
}
.img4 img{
  width: 150%;
  height: 150%;
  transform: translate(-135px,-115px);
}
.con_txt{
  width:40%;
}

